	module.exports = {"data":{"postsJson":{"title":"The King Warns & Encourages","questionsTitle":"COMMANDS & PROMISES","display":"block","bookName":"Matthew","ref":"24:1-25:46","html":"<p>   <strong>24</strong><u>1</u>Jesus went out from the temple, and was going on his way. His disciples came to him to show him the buildings of the temple. <u>2</u>But he answered them,</p><h1>JESUS</h1><h2>You see all of these things, don’t you? Most certainly I tell you, there will not be left here one stone on another, that will not be thrown down.</h2><p>   <u>3</u>As he sat on the Mount of Olives, the disciples came to him privately, saying,</p><h1>THE DISCIPLES</h1><h3>Tell us, when will these things be? What is the sign of your coming, and of the end of the age?</h3><p>   <u>4</u>Jesus answered them,</p><h1>JESUS</h1><h2>Be careful that no one leads you astray. <u>5</u>For many will come in my name, saying, ‘I am the Christ,’ and will lead many astray. <u>6</u>You will hear of wars and rumors of wars. See that you aren’t troubled, for all this must happen, but the end is not yet. <u>7</u>For nation will rise against nation, and kingdom against kingdom; and there will be famines, plagues, and earthquakes in various places. <u>8</u>But all these things are the beginning of birth pains. <u>9</u>Then they will deliver you up to oppression, and will kill you. You will be hated by all of the nations for my name’s sake. <u>10</u>Then many will stumble, and will deliver up one another, and will hate one another. <u>11</u>Many false prophets will arise, and will lead many astray. <u>12</u>Because iniquity will be multiplied, the love of many will grow cold. <u>13</u>But he who endures to the end, the same will be saved. <u>14</u>This Good News of the Kingdom will be preached in the whole world for a testimony to all the nations, and then the end will come.<br />   <u>15</u>When, therefore, you see the abomination of desolation,* which was spoken of through Daniel the prophet, standing in the holy place (let the reader understand), <u>16</u>then let those who are in Judea flee to the mountains. <u>17</u>Let him who is on the housetop not go down to take out things that are in his house. <u>18</u>Let him who is in the field not return back to get his clothes. <u>19</u>But woe to those who are with child and to nursing mothers in those days! <u>20</u>Pray that your flight will not be in the winter, nor on a Sabbath, <u>21</u>for then there will be great oppression, such as has not been from the beginning of the world until now, no, nor ever will be. <u>22</u>Unless those days had been shortened, no flesh would have been saved. But for the sake of the chosen ones, those days will be shortened.<br />   <u>23</u>Then if any man tells you, ‘Behold, here is the Christ,’ or, ‘There,’ don’t believe it. <u>24</u>For there will arise false christs, and false prophets, and they will show great signs and wonders, so as to lead astray, if possible, even the chosen ones.<br />   <u>25</u>Behold, I have told you beforehand. <u>26</u>If therefore they tell you, ‘Behold, he is in the wilderness,’ don’t go out; ‘Behold, he is in the inner rooms,’ don’t believe it. <u>27</u>For as the lightning flashes from the east, and is seen even to the west, so will be the coming of the Son of Man. <u>28</u>For wherever the carcass is, there is where the vultures* gather together. <u>29</u>But immediately after the oppression of those days, the sun will be darkened, the moon will not give its light, the stars will fall from the sky, and the powers of the heavens will be shaken;* <u>30</u>and then the sign of the Son of Man will appear in the sky. Then all the tribes of the earth will mourn, and they will see the Son of Man coming on the clouds of the sky with power and great glory. <u>31</u>He will send out his angels with a great sound of a trumpet, and they will gather together his chosen ones from the four winds, from one end of the sky to the other.<br />   <u>32</u>Now from the fig tree learn this parable. When its branch has now become tender, and produces its leaves, you know that the summer is near. <u>33</u>Even so you also, when you see all these things, know that it is near, even at the doors. <u>34</u>Most certainly I tell you, this generation* will not pass away, until all these things are accomplished. <u>35</u>Heaven and earth will pass away, but my words will not pass away. <u>36</u>But no one knows of that day and hour, not even the angels of heaven,* but my Father only.<br />   <u>37</u>As the days of Noah were, so will be the coming of the Son of Man. <u>38</u>For as in those days which were before the flood they were eating and drinking, marrying and giving in marriage, until the day that Noah entered into the ship, <u>39</u>and they didn’t know until the flood came, and took them all away, so will be the coming of the Son of Man. <u>40</u>Then two men will be in the field: one will be taken and one will be left; <u>41</u>two women grinding at the mill, one will be taken and one will be left. <u>42</u>Watch therefore, for you don’t know in what hour your Lord comes. <u>43</u>But know this, that if the master of the house had known in what watch of the night the thief was coming, he would have watched, and would not have allowed his house to be broken into. <u>44</u>Therefore also be ready, for in an hour that you don’t expect, the Son of Man will come.<br />   <u>45</u>Who then is the faithful and wise servant, whom his lord has set over his household, to give them their food in due season? <u>46</u>Blessed is that servant whom his lord finds doing so when he comes. <u>47</u>Most certainly I tell you that he will set him over all that he has. <u>48</u>But if that evil servant should say in his heart, ‘My lord is delaying his coming,’ <u>49</u>and begins to beat his fellow servants, and eat and drink with the drunkards, <u>50</u>the lord of that servant will come in a day when he doesn’t expect it, and in an hour when he doesn’t know it, <u>51</u>and will cut him in pieces, and appoint his portion with the hypocrites. There is where the weeping and grinding of teeth will be.<br />   <strong>25</strong><u>1</u>Then the Kingdom of Heaven will be like ten virgins, who took their lamps, and went out to meet the bridegroom. <u>2</u>Five of them were foolish, and five were wise. <u>3</u>Those who were foolish, when they took their lamps, took no oil with them, <u>4</u>but the wise took oil in their vessels with their lamps. <u>5</u>Now while the bridegroom delayed, they all slumbered and slept. <u>6</u>But at midnight there was a cry, ‘Behold! The bridegroom is coming! Come out to meet him!’ <u>7</u>Then all those virgins arose, and trimmed their lamps.* <u>8</u>The foolish said to the wise, ‘Give us some of your oil, for our lamps are going out.’ <u>9</u>But the wise answered, saying, ‘What if there isn’t enough for us and you? You go rather to those who sell, and buy for yourselves.’ <u>10</u>While they went away to buy, the bridegroom came, and those who were ready went in with him to the marriage feast, and the door was shut. <u>11</u>Afterward the other virgins also came, saying, ‘Lord, Lord, open to us.’ <u>12</u>But he answered, ‘Most certainly I tell you, I don’t know you.’ <u>13</u>Watch therefore, for you don’t know the day nor the hour in which the Son of Man is coming.<br />   <u>14</u>For it is like a man, going into another country, who called his own servants, and entrusted his goods to them. <u>15</u>To one he gave five talents,* to another two, to another one; to each according to his own ability. Then he went on his journey. <u>16</u>Immediately he who received the five talents went and traded with them, and made another five talents. <u>17</u>In the same way, he also who got the two gained another two. <u>18</u>But he who received the one talent went away and dug in the earth, and hid his lord’s money.<br />   <u>19</u>Now after a long time the lord of those servants came, and reconciled accounts with them. <u>20</u>He who received the five talents came and brought another five talents, saying, ‘Lord, you delivered to me five talents. Behold, I have gained another five talents besides them.’<br />   <u>21</u>His lord said to him, ‘Well done, good and faithful servant. You have been faithful over a few things, I will set you over many things. Enter into the joy of your lord.’<br />   <u>22</u>He also who got the two talents came and said, ‘Lord, you delivered to me two talents. Behold, I have gained another two talents besides them.’<br />   <u>23</u>His lord said to him, ‘Well done, good and faithful servant. You have been faithful over a few things, I will set you over many things. Enter into the joy of your lord.’<br />   <u>24</u>He also who had received the one talent came and said, ‘Lord, I knew you that you are a hard man, reaping where you did not sow, and gathering where you did not scatter. <u>25</u>I was afraid, and went away and hid your talent in the earth. Behold, you have what is yours.’<br />   <u>26</u>But his lord answered him, ‘You wicked and slothful servant. You knew that I reap where I didn’t sow, and gather where I didn’t scatter. <u>27</u>You ought therefore to have deposited my money with the bankers, and at my coming I should have received back my own with interest. <u>28</u>Take away therefore the talent from him, and give it to him who has the ten talents. <u>29</u>For to everyone who has will be given, and he will have abundance, but from him who doesn’t have, even that which he has will be taken away. <u>30</u>Throw out the unprofitable servant into the outer darkness, where there will be weeping and gnashing of teeth.’<br />   <u>31</u>But when the Son of Man comes in his glory, and all the holy angels with him, then he will sit on the throne of his glory. <u>32</u>Before him all the nations will be gathered, and he will separate them one from another, as a shepherd separates the sheep from the goats. <u>33</u>He will set the sheep on his right hand, but the goats on the left. <u>34</u>Then the King will tell those on his right hand, ‘Come, blessed of my Father, inherit the Kingdom prepared for you from the foundation of the world; <u>35</u>for I was hungry, and you gave me food to eat. I was thirsty, and you gave me drink. I was a stranger, and you took me in. <u>36</u>I was naked, and you clothed me. I was sick, and you visited me. I was in prison, and you came to me.’<br />   <u>37</u>Then the righteous will answer him, saying, ‘Lord, when did we see you hungry, and feed you; or thirsty, and give you a drink? <u>38</u>When did we see you as a stranger, and take you in; or naked, and clothe you? <u>39</u>When did we see you sick, or in prison, and come to you?’<br />   <u>40</u>The King will answer them, ‘Most certainly I tell you, because you did it to one of the least of these my brothers* , you did it to me.’ <u>41</u>Then he will say also to those on the left hand, ‘Depart from me, you cursed, into the eternal fire which is prepared for the devil and his angels; <u>42</u>for I was hungry, and you didn’t give me food to eat; I was thirsty, and you gave me no drink; <u>43</u>I was a stranger, and you didn’t take me in; naked, and you didn’t clothe me; sick, and in prison, and you didn’t visit me.’<br />   <u>44</u>Then they will also answer, saying, ‘Lord, when did we see you hungry, or thirsty, or a stranger, or naked, or sick, or in prison, and didn’t help you?’<br />   <u>45</u>Then he will answer them, saying, ‘Most certainly I tell you, because you didn’t do it to one of the least of these, you didn’t do it to me.’ <u>46</u>These will go away into eternal punishment, but the righteous into eternal life.</h2><h5>*Matthew 24:15 Daniel 9:27; 11:31; 12:11<br />*Matthew 24:28 or, eagles<br />*Matthew 24:29 Isaiah 13:10; 34:4<br />*Matthew 24:34 The word for “generation” (genea) can also be translated as “race.”<br />*Matthew 24:36 NU adds “nor the son”<br />*Matthew 25:7 The end of the wick of an oil lamp needs to be cut off periodically to avoid having it become clogged with carbon deposits. The wick height is also adjusted so that the flame burns evenly and gives good light without producing a lot of smoke.<br />*Matthew 25:15 A talent is about 30 kilograms or 66 pounds (usually used to weigh silver unless otherwise specified)<br />*Matthew 25:40 The word for “brothers” here may be also correctly translated “brothers and sisters” or “siblings.”</h5>","path":"/313","Question1":"What God-given instructions do you note in this portion of Scripture?","Question2":"Put yourself in the original setting and creatively think about what it might have been like to receive this instruction. How does this relate to you where you live and work today?","Question3":"What God-inspired hope do you note in this portion of Scripture? How can you grow to have more of this in your own life?","Question4":"Commit yourself to tell a friend about what this hope means for you today.","prevPath":"/312","nextPath":"/314"}},"pathContext":{}}

